import styled from "styled-components";


const Container = styled.div`
  height: 30px;
  background-color: teal;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  font-weight: 500;
`;



const Know = () => {
	return (
		<Container>
			Super Deal! Free Shiping on Orders Over $50
		</Container>
	)
};

export default Know
